import styled, { css } from 'styled-components';
import theme from '../theme';

// Styled component definition for a button
export const StyledButtonComponent = styled.button`
  height: 38px;
  width: max-content;
  padding: 11px 28px;
  cursor: pointer;
  border: 2px solid;
  border-radius: 4px;
  font-size: 14px;
  font-family: Arial;
  font-weight: 600;
  margin-top: 10px;
  margin-right: 10px;

  ${({ type, isOutline, border }) => css`
    background-color: ${isOutline ? 'transparent' : theme?.[type]};
    color: ${isOutline ? theme?.[type] : 'white'};
    border-color: ${border ? 'transparent' : theme?.[type]};
  `}

  ${css`
    padding: 10px 20px;
    text-align: center;
    text-decoration: none;
    font-size: 16px;
    margin-left: 2%;
    cursor: pointer;

    ${({ type, isOutline, disableHover }) => css`
      background-color: ${isOutline ? 'transparent' : theme?.[type]};
      color: ${isOutline ? theme?.[type] : 'white'};

      ${!disableHover &&
      css`
        &:hover,
        &:active {
          background-color: ${isOutline ? theme?.[type] : theme?.[type]};
          color: white;
        }
      `}
    `}
  `}
`;
